@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: "Poppins", sans-serif !important;
  background: #f8f8f8 !important;
  color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* header */

.header-sroll {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  z-index: 99999;
  background-color: #0779e4;
  padding: 20px 0px 20px 0px !important;
}

.nav-item {
  padding-left: 35px;
}

a {
  color: #f8f8f8 !important;
  text-decoration: none !important;
}

.nav-item .active {
  background-color: #3e9ff9;
  padding: 25px 15px 29px 15px;
  color: #fff !important;
  position: relative;
}

.nav-item .active::after {
  position: absolute;
  bottom: -9px;
  left: 50%;
  transform: translateX(-50%);
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10px 10px 0 10px;
  border-color: #3e9ff9 transparent transparent transparent;
}

/* HomePage */

.center-vertically {
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  display: flex;
}

.font-bold {
  font-weight: bold;
}

.font-600 {
  font-weight: 600;
}

.text-40 {
  font-size: 43px;
}

.title {
  background-color: #fff;
  color: #0779e4;
  width: 72px;
  font-size: 13px;
  font-weight: 600;
  border-radius: 6px;
  position: relative;
}

.title::after {
  position: absolute;
  bottom: -6px;
  left: 50%;
  transform: translateX(-50%);
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10px 10px 0 10px;
  border-color: #fff transparent transparent transparent;
}

.line-height-60 {
  line-height: 60px;
}

/* About */

.custom-btn {
  background-color: #fff;
  color: #7f7f7f;
  border: none;
  outline: none;
  border-radius: 7px;
  position: relative;
}

.tabs .active {
  background-color: #0779e4;
  color: #fff;
  border: none;
  outline: none;
}

.tabs .active::after {
  position: absolute;
  bottom: -9px;
  left: 50%;
  transform: translateX(-50%);
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10px 10px 0 10px;
  border-color: #0779e4 transparent transparent transparent;
}

.text-color {
  color: #a1a1a1;
}

.mobile-padd-about{
  padding-left: 3rem !important;
  padding-right: 3rem !important;
}

/* service */

.icon {
  width: 70px;
  height: 60px;
  background-color: #0779e4;
  flex-direction: column;
  justify-content: center;
  display: flex;
  position: relative;
}

.service-icon {
  font-size: 2.5rem !important;
  color: #fff;
  margin-left: 13px;
}

.icon::after {
  position: absolute;
  bottom: -9px;
  left: 50%;
  transform: translateX(-50%);
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10px 10px 0 10px;
  border-color: #0779e4 transparent transparent transparent;
}

/* protfolio */

.protfolio-card {
  position: relative;
  width: 45% !important;
}

.image {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  transition: 0.5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.middle {
  opacity: 0;
  position: absolute;
  text-align: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  content: "";
  background: #0779e4;
  z-index: -1;
  transition: all 0.3s ease;
}

.protfolio-card:hover .image {
  opacity: 0.3;
}

.protfolio-card:hover .middle {
  opacity: 1;
}

.text {
  color: white;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

/* Testimonial */

.carousel-indicators {
  bottom: -45px !important;
}


/* footer  */

.footer {
  background-color: #eee;
}

.second-footer {
  background-color: #e9e9e9;
}

.footer-color {
  color: #000 !important;
  cursor: pointer;
}

ul li {
  list-style-type: none;
}

.whatappp {
  color: #3bd38c !important;
  border: none !important;
  height: 50px;
  width: 60px;
  background-color: transparent;
  bottom: 40px;
  right: 30px;
  position: fixed;
}

.custom-whatapp {
  background-color: transparent;
  font-size: 40px !important;
  border-radius: 10px !important;
}
.margin-3rem{
  margin-right: 3rem;
}
.padding-right-4{
  padding-right: 4rem;
}
.navrbar-collapse-item{
  margin-top: 20px;
  line-height: 40px;
  margin-left: -27px;
}
.mobile-navbar-bg-color{
  background-color: #3e9ff9;
}

@media only screen and (max-width: 320px) {
  .mobile-responsive-padding-margin {
    margin-left: 0px !important;
  }

  .protfolio-card {
    width: 100% !important;
  }

  .mobile-height-auto {
    height: auto !important;
    border-radius: 7px;
  }
  .mobile-padding-counter{
    padding-bottom: 4rem !important;
  }
  .mobile-marg-btn-mesg{
    margin-bottom: 15px !important;
  }
  .mobile-padd-about{
    margin-top: 15px !important;
    padding-left: 15px !important;
    padding-right: 10px !important;
  }
  .margin-3rem{
    margin-right: .6rem;
  }
  .mobile-margin-bottom{
    margin-bottom: 20px !important;
  }
  .padding-right-4{
    padding-right: .3rem !important;
  }
}

